function scrollTitleChange(e, num, datas) {
	console.log('-----------------------title-------------------');
	console.log(e, num, datas, '---------e:---------num----------------------datas:index---------------');
	let view = uni.createSelectorQuery().in(this).select('#' + e);
	view.fields({
		size: true,
		scrollOffset: true
	}, data => {}).exec();

	view.boundingClientRect(data => {
		console.log(data, 'data')
		console.log(data, "" + data.top);
		let top = Number(data.top);
		if (top <= num) { // 
			this.$emit('tap-ChangeTitle', datas);
		}
	}).exec();
}
export {
	scrollTitleChange
}
