
function landRegistra() {










































    	console.log('------------------------------------------');

    		console.log('');










    	if (!uni.getStorageSync('landRegist')) { //   
    	  uni.navigateTo({
    		url: '/pages/landRegistration/mp/landRegistration'
    	  });
		  return false;
    	} else if (uni.getStorageSync('landRegist')) { //  token
    		let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 
    		let params = {}; //  
    		uni.showLoading({ // loading
    			title: ''
    		});
    		uni.request({
    			url: this.api2 + '/server/list', //
    			header: {
    				Authorization:"Bearer "+landRegistLG.token//token
    			},
    			success: (response) => {
    				console.log(response.data.code);
    				if (String(response.data.code) === '700') {
    					uni.navigateTo({
    						url: '/pages/landRegistration/mp/landRegistration'
    					});
						return false;
    				}
    				uni.hideLoading(); //  loading
    			},
    			fail: (error) => {
    				uni.hideLoading(); //  loading
    				uni.showToast({
    					title: '',
    					icon: 'none',
    					duration: 1000
    				});
    				console.log(error, '');
    			}
    		});
    	}

};
export {
    landRegistra
};