import {api1,api2,api3} from '@/static/mbcJs/api';
console.log(api1, api2, api3);
import {landRegistra} from '@/static/mbcJs/landRegistra';
function resetUrl (url) {
	let A = 'from=singlemessage&isappinstalled=0'; // 
	let B = 'from=timeline&isappinstalled=0'; // 
	let C = 'groupmessage&isappinstalled=0'; // 
	if (url.indexOf(A) !== -1) {
		return url.split(A)[0] + encodeURIComponent(A);
	} else if (url.indexOf(B) !== -1) {
		return url.split(B)[0] +  encodeURIComponent(B);
	} else if (url.indexOf(C) !== -1) {
		return url.split(C)[0] +  encodeURIComponent(C);
	} else {
		return url;
	}
};
function shareSdkConfig () {
	// let resetUrls = resetUrl(window.location.href);
	let urlParams = window.location.href.split("#")[0];
	var u = navigator.userAgent;
	var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
	var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
	if (isAndroid === true) {
		// alert('027:' + urlParams);
	} else {
		// urlParams =  uni.getStorageSync('shareConfigUrl');
		// alert('ios:027' + urlParams);
	}
	urlParams = urlParams.replace(/\&/g, '%26');
	let params = {
		url: urlParams
	}; //  
	return new Promise(resolve => {
		uni.showLoading({
			// loading
			title: ''
		});
		uni.request({
			url: api2 + '/rest-rp/wechat/portal/createJsapiSignature', //
			data: params,
			method: 'POST',
			header: {
				Authorization: 'Bearer ' //token
			},
			success: response => {
				console.log(response.data, '---------------response.data--------------')
				if (response.data.ret === '200') {
					uni.hideLoading(); //  loading
					console.log('SDK')
					resolve(response.data.content);
				} else {
					uni.hideLoading(); //  loading
					uni.showToast({
						title: '',
						icon: 'none',
						duration: 1000
					});
				}
			},
			fail: error => {
				uni.hideLoading(); //  loading
				uni.showToast({
					title: '',
					icon: 'none',
					duration: 1000
				});
				console.log(error, '');
			}
		});
	});
}

export {
	shareSdkConfig
}