function mergeBasicData (data, type, newsItems, indexNum) {
	console.log(data, '---------------------------------');
	console.log(type, '------------------type------------------');
	console.log(newsItems, '----------------------------------');
	console.log(indexNum, '')
	data.map((items, index)=>{
		if(items.type === type && index === indexNum) {
			// items = null;
			// items = newsItems;
			data.splice(index,1,newsItems);
		}
	});
	return data
}

export {
	mergeBasicData
}