
function keyText (e) { // keyText
	console.log(e, typeof(e), '');
	if (e === 'title') {
		return ''
	} else if (e === 'content' || e === 'content1' || e === 'content2') {
		return ''
	} else if (e === 'content3') {
		return '50'
	} else if (e === 'key' || e === 'key1' || e === 'key2') {
		return ''
	} else if (e === 'value' || e === 'value1' || e === 'value2' || e === 'value3') {
		return ''
	} else if (e === 'addres') {
		return ''
	} else if (e === 'tel1' || e === 'tel2') {
		return ''
	} else {
		return ''
	}
}

export {keyText}