import {
	api1,
	api2,
	api3
} from '@/static/mbcJs/api';
console.log(api1, api2, api3);
let api2Url = api2;
let returnValue = {
	success: false, // 
	data: null, // 
	type: null, // 
}
let sorce = null; // 0  1   2 
let countType = null; // 
// --------------------------------------------------
// 0
// 1
// 6
// 8
// --------------------------------------------------
// type 0 ''
// type 1 ''''
// type 2 ''''
// type 3 ''''
// type 4 ''
// type 5 ''''
// type 6 ''''
// type 7 
// type 8 
// type 9 
// type 10 
let params = null; // 
let routeParam = null; // 
function calculateCost(way, paramsUrl, paramsJSH, classify) {
	// way:get post
	// paramsUrl: 
	// paramsJSH 
	console.log(way, paramsUrl, paramsJSH);
	return new Promise(resolve => {
		uni.showLoading({
			// loading
			title: ''
		});
		uni.request({
			url: api2Url + paramsUrl, //
			data: paramsJSH,
			method: way,
			header: {
				Authorization: 'Bearer '//token
			},
			success: response => {
				console.log(response.data, '---------------response.data--------------')
				uni.hideLoading(); //  loading
				returnValue.success = true;
				returnValue.data = response.data;
				returnValue.type = paramsJSH.type;
				console.log(returnValue, '');
				resolve(returnValue)
			},
			fail: error => {
				uni.hideLoading(); //  loading
				uni.showToast({
					title: '',
					icon: 'none',
					duration: 1000
				});
				console.log(error, '');
				returnValue.success = false;
				returnValue.data = 0;
				returnValue.type = paramsJSH.type;
				resolve(returnValue)
			}
		});
	})
}

async function platformAccount(CYDsorce, CYDcountType, CYDparams, CYDrouteParam) {
	sorce = CYDsorce; // 0  1   2 
	countType = CYDcountType; // 
	params = CYDparams; // 
	routeParam = CYDrouteParam; // 
	let tempType = 0; //  0 1
	if (routeParam.tempType && routeParam.tempType  === 1) {
		return await calculateCost('GET', '/rest-rp/purc/setPurcDate', params);
	} else {
		if (sorce === 0) {
			return await calculateCost('GET', '/rest-rp/instruc/instrucDate', params);
		} else if (sorce === 1) {
			return await calculateCost('GET', '/rest-rp/instruc/instrucDate', params);
			return await calculateCost('POST', '/rest-rp/adve/setAdveData', params);
		}
	}
}
export {
	platformAccount
}
