import {
	api1,
	api2,
	api3
} from '@/static/mbcJs/api';
console.log(api1, api2, api3);
import {landRegistra} from '@/static/mbcJs/landRegistra';
let api2Url = api2;
let returnValue = {
	success: false, // 
	data: null, // 
	type: null, // 
}
let params = null; // 
function focusStoreCount(way, paramsUrl, paramsUrl2, paramsJSH) {
	// way:get post
	// paramsUrl: 
	// paramsJSH 
	console.log(way, paramsUrl, paramsUrl2, paramsJSH);
	return new Promise(resolve => {
			// uni.showLoading({
			// 	// loading
			// 	title: ''
			// });
			if (!uni.getStorageSync('landRegist')) {
				landRegistra(); // 
			}
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 
				console.log(landRegistLG.user.id);
				uni.request({
					url: api2Url + paramsUrl, //
					data: paramsJSH,
					method: way,
					header: {
						Authorization: "Bearer " + landRegistLG.token //token
					},
					success: response => {
						console.log(response.data, '---------------response.data--------------')
						// uni.hideLoading(); //  loading
						if (response.data.ret === '200') {
							returnValue.success = true;
							resolve(returnValue)
						}  else if (response.data.ret === '202') {
							console.log('202');
							uni.removeStorageSync('landRegist');
							uni.removeStorageSync('clickItems');
							uni.showToast({
								title: '',
								icon: 'none',
								duration: 1000
							});
							if (!uni.getStorageSync('landRegist')) {
								landRegistra(); // 
							}
						}else if (response.data.ret === '400') {
							uni.request({
								url: api2Url + paramsUrl2, //
								data: paramsJSH,
								method: way,
								header: {
									Authorization: "Bearer " + landRegistLG.token //token
								},
								success: response => {
									console.log(response.data, '---------------response.data--------------')
									// uni.hideLoading(); //  loading
									if (response.data.ret === '200') {
										returnValue.success = false;
										resolve(returnValue)
									}  else if (response.data.ret === '202') {
										console.log('202');
										uni.removeStorageSync('landRegist');
										uni.removeStorageSync('clickItems');
										uni.showToast({
											title: '',
											icon: 'none',
											duration: 1000
										});
										if (!uni.getStorageSync('landRegist')) {
											landRegistra(); // 
										}
									}else if (response.data.ret === '400') {
										uni.showToast({
											title: response.data.msg,
											icon: 'none',
											duration: 1000
										});
									};
									returnValue.data = response.data;
									returnValue.type = paramsJSH.type;
									console.log(returnValue, '')
								},
								fail: error => {
									// uni.hideLoading(); //  loading
									returnValue.data = 0;
									returnValue.type = paramsJSH.type;
									uni.showToast({
										title: '',
										icon: 'none',
										duration: 1000
									});
									console.log(error, '');
									console.log(returnValue, '');
									resolve(returnValue)
								}
							});
						};
						returnValue.data = response.data;
						returnValue.type = paramsJSH.type;
						console.log(returnValue, '')
					},
					fail: error => {
						// uni.hideLoading(); //  loading
						returnValue.success = false;
						returnValue.data = 0;
						returnValue.type = paramsJSH.type;
						uni.showToast({
							title: '',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '');
						console.log(returnValue, '');
						resolve(returnValue)
					}
				});
			}
		})
	}

	async function focusStore(CYDparams) {
		params = CYDparams;
		return await focusStoreCount('GET', '/rest-rp/follow/user', '/rest-rp/follow/cancelUser', params);
	}
	export {
		focusStore
	}
